import { db } from '../db';
import { paymentGateways, plans, checkouts, orderBumps } from '../db/schema';
import { eq, inArray } from 'drizzle-orm';

// --- Types ---

interface OrderBumpItem {
  id: number;
  name: string;
  price: number;
}

export interface PixChargeRequest {
  customerName: string;
  customerEmail: string;
  customerDocument: string;
  customerPhone: string;
  totalAmount: number; // em centavos
  planId: number;
  orderBump: boolean; // Retrocompatibilidade
  orderBumpIds?: number[]; // IDs das order bumps selecionadas
}

export interface PixChargeResponse {
  success: true;
  transactionId: number;
  checkoutId: number; // ID do checkout no nosso banco
  pixQrCode: string;
  pixUrl: string;
  expirationDate: string;
  status: string;
}

export interface PixChargeError {
  success: false;
  error: string;
  code: 'GATEWAY_NOT_CONFIGURED' | 'GATEWAY_INACTIVE' | 'INVALID_DATA' | 'API_ERROR' | 'UNEXPECTED_RESPONSE' | 'CARD_REFUSED';
}

export type PixChargeResult = PixChargeResponse | PixChargeError;

// --- Credit Card Types ---

export interface CardChargeRequest {
  customerName: string;
  customerEmail: string;
  customerDocument: string;
  customerPhone: string;
  totalAmount: number; // em centavos
  planId: number;
  orderBump: boolean; // Retrocompatibilidade
  orderBumpIds?: number[]; // IDs das order bumps selecionadas
  cardHash: string; // Token gerado pelo JunglePagamentos.encrypt()
  installments: number;
}

export interface CardChargeResponse {
  success: true;
  transactionId: number;
  checkoutId: number; // ID do checkout no nosso banco
  status: string;
  cardLastDigits: string;
  cardBrand: string;
  installments: number;
}

export type CardChargeResult = CardChargeResponse | PixChargeError;

// Resposta da API JunglePay
interface JunglePayTransactionResponse {
  id: number;
  status: string;
  amount: number;
  secureUrl?: string;
  installments?: number;
  pix?: {
    qrcode: string;
    expirationDate: string;
    end2EndId?: string | null;
    receiptUrl?: string | null;
  };
  card?: {
    id: number;
    brand: string;
    holderName: string;
    lastDigits: string;
    expirationMonth: number;
    expirationYear: number;
  };
  refusedReason?: {
    code: string;
    message: string;
  } | null;
  customer?: any;
  items?: any[];
}

// --- Service ---

export class JunglePayService {

  /**
   * Verifica se JunglePay est configurado e ativo
   */
  static async getActiveGateway() {
    const [gateway] = await db
      .select()
      .from(paymentGateways)
      .where(eq(paymentGateways.name, 'JunglePay'))
      .limit(1);

    return gateway;
  }

  /**
   * Gera a autenticao Basic Auth para a API JunglePay
   */
  static generateAuthHeader(secretKey: string): string {
    const credentials = `${secretKey}:x`;
    // Usando btoa para base64 encode (disponvel no Bun)
    const base64Credentials = btoa(credentials);
    return `Basic ${base64Credentials}`;
  }

  /**
   * Remove caracteres especiais do CPF (000.000.000-00 -> 00000000000)
   */
  static sanitizeDocument(document: string): string {
    return document.replace(/\D/g, '');
  }

  /**
   * Remove caracteres especiais do telefone ((00) 00000-0000 -> 0000000000)
   */
  static sanitizePhone(phone: string): string {
    return phone.replace(/\D/g, '');
  }

  /**
   * Busca o plano pelo ID
   */
  static async getPlanById(planId: number) {
    const [plan] = await db
      .select()
      .from(plans)
      .where(eq(plans.id, planId))
      .limit(1);

    return plan;
  }

  /**
   * Busca order bumps por IDs
   */
  static async getOrderBumpsByIds(ids: number[]): Promise<OrderBumpItem[]> {
    if (!ids || ids.length === 0) return [];
    
    const bumps = await db
      .select({ id: orderBumps.id, name: orderBumps.name, price: orderBumps.price })
      .from(orderBumps)
      .where(inArray(orderBumps.id, ids));
    
    return bumps;
  }

  /**
   * Cria uma cobrana PIX via JunglePay
   */
  static async createPixCharge(request: PixChargeRequest): Promise<PixChargeResult> {
    // 1. Verificar se JunglePay est configurado e ativo
    const gateway = await this.getActiveGateway();

    if (!gateway) {
      return {
        success: false,
        error: 'Gateway JunglePay no est configurado no sistema.',
        code: 'GATEWAY_NOT_CONFIGURED'
      };
    }

    if (!gateway.isActive) {
      return {
        success: false,
        error: 'Gateway JunglePay est configurado mas no est ativo.',
        code: 'GATEWAY_INACTIVE'
      };
    }

    if (!gateway.secretKey) {
      return {
        success: false,
        error: 'Chave secreta da JunglePay no est configurada.',
        code: 'GATEWAY_NOT_CONFIGURED'
      };
    }

    // 2. Validar dados obrigatrios
    if (!request.customerName || !request.customerEmail || !request.customerDocument) {
      return {
        success: false,
        error: 'Dados do cliente incompletos: nome, email e CPF so obrigatrios.',
        code: 'INVALID_DATA'
      };
    }

    if (!request.totalAmount || request.totalAmount <= 0) {
      return {
        success: false,
        error: 'Valor total invlido.',
        code: 'INVALID_DATA'
      };
    }

    // 3. Buscar informaes do plano
    const plan = await this.getPlanById(request.planId);

    if (!plan) {
      return {
        success: false,
        error: 'Plano no encontrado.',
        code: 'INVALID_DATA'
      };
    }

    // 3.1 Validar se o plano aceita PIX
    if (plan.acceptsPix === false) {
      return {
        success: false,
        error: 'Este plano no aceita pagamento via PIX.',
        code: 'INVALID_DATA'
      };
    }

    // 4. Montar o payload para a API JunglePay
    const items: Array<{ title: string; unitPrice: number; quantity: number; tangible: boolean }> = [
      {
        title: plan.name,
        unitPrice: plan.price,
        quantity: 1,
        tangible: false
      }
    ];

    // Adicionar order bumps selecionadas (nova lgica com mltiplas bumps)
    if (request.orderBumpIds && request.orderBumpIds.length > 0) {
      const selectedBumps = await this.getOrderBumpsByIds(request.orderBumpIds);
      for (const bump of selectedBumps) {
        items.push({
          title: bump.name,
          unitPrice: bump.price,
          quantity: 1,
          tangible: false
        });
      }
    } else if (request.orderBump) {
      // Retrocompatibilidade: order bump nica legada
      items.push({
        title: 'Acesso Antecipado Premium',
        unitPrice: 1990, // R$ 19,90
        quantity: 1,
        tangible: false
      });
    }

    const payload = {
      paymentMethod: 'pix',
      amount: request.totalAmount,
      customer: {
        name: request.customerName,
        email: request.customerEmail,
        phone: this.sanitizePhone(request.customerPhone),
        document: {
          number: this.sanitizeDocument(request.customerDocument),
          type: 'cpf'
        }
      },
      items,
      pix: {
        expiresInDays: 1
      },
      // Usar postbackUrl do banco de dados
      postbackUrl: gateway.postbackUrl || undefined
    };

    // 5. Fazer a chamada HTTP para a API JunglePay
    try {
      const response = await fetch('https://api.junglepagamentos.com/v1/transactions', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': this.generateAuthHeader(gateway.secretKey)
        },
        body: JSON.stringify(payload)
      });

      if (!response.ok) {
        const errorData = await response.text();
        console.error('[JunglePay] API Error:', response.status, errorData);
        return {
          success: false,
          error: `Erro na API JunglePay: ${response.status} - ${errorData}`,
          code: 'API_ERROR'
        };
      }

      const data: JunglePayTransactionResponse = await response.json();

      // 6. Validar resposta
      if (!data.pix || !data.pix.qrcode) {
        console.error('[JunglePay] Resposta inesperada:', data);
        return {
          success: false,
          error: 'Resposta da API JunglePay no contm dados do PIX.',
          code: 'UNEXPECTED_RESPONSE'
        };
      }

      // 7. Criar registro do checkout no banco com o externalId
      const [checkout] = await db.insert(checkouts).values({
        planId: request.planId,
        externalId: String(data.id), // ID da transao na JunglePay
        paymentMethod: 'pix',
        orderBump: request.orderBump || (request.orderBumpIds && request.orderBumpIds.length > 0),
        orderBumpIds: request.orderBumpIds || null,
        totalAmount: request.totalAmount,
        customerName: request.customerName,
        customerEmail: request.customerEmail,
        customerDocument: this.sanitizeDocument(request.customerDocument),
        customerPhone: this.sanitizePhone(request.customerPhone),
        status: 'pending'
      }).returning();

      // 8. Retornar dados do PIX
      return {
        success: true,
        transactionId: data.id,
        checkoutId: checkout.id, // ID do checkout no nosso banco
        pixQrCode: data.pix.qrcode,
        pixUrl: data.secureUrl || '',
        expirationDate: data.pix.expirationDate,
        status: data.status
      };

    } catch (error: any) {
      console.error('[JunglePay] Request Error:', error);
      return {
        success: false,
        error: `Erro ao conectar com JunglePay: ${error.message}`,
        code: 'API_ERROR'
      };
    }
  }

  /**
   * Cria uma cobrana de carto de crdito via JunglePay
   */
  static async createCardCharge(request: CardChargeRequest): Promise<CardChargeResult> {
    // 1. Verificar se JunglePay est configurado e ativo
    const gateway = await this.getActiveGateway();

    if (!gateway) {
      return {
        success: false,
        error: 'Gateway JunglePay no est configurado no sistema.',
        code: 'GATEWAY_NOT_CONFIGURED'
      };
    }

    if (!gateway.isActive) {
      return {
        success: false,
        error: 'Gateway JunglePay est configurado mas no est ativo.',
        code: 'GATEWAY_INACTIVE'
      };
    }

    if (!gateway.secretKey) {
      return {
        success: false,
        error: 'Chave secreta da JunglePay no est configurada.',
        code: 'GATEWAY_NOT_CONFIGURED'
      };
    }

    // 2. Validar dados obrigatrios
    if (!request.customerName || !request.customerEmail || !request.customerDocument) {
      return {
        success: false,
        error: 'Dados do cliente incompletos: nome, email e CPF so obrigatrios.',
        code: 'INVALID_DATA'
      };
    }

    if (!request.cardHash) {
      return {
        success: false,
        error: 'Token do carto no foi fornecido.',
        code: 'INVALID_DATA'
      };
    }

    if (!request.totalAmount || request.totalAmount <= 0) {
      return {
        success: false,
        error: 'Valor total invlido.',
        code: 'INVALID_DATA'
      };
    }

    // 3. Buscar informaes do plano
    const plan = await this.getPlanById(request.planId);

    if (!plan) {
      return {
        success: false,
        error: 'Plano no encontrado.',
        code: 'INVALID_DATA'
      };
    }

    // 3.1 Validar se o plano aceita Carto de Crdito
    if (plan.acceptsCard === false) {
      return {
        success: false,
        error: 'Este plano no aceita pagamento via Carto de Crdito.',
        code: 'INVALID_DATA'
      };
    }

    // 4. Montar o payload para a API JunglePay
    const items: Array<{ title: string; unitPrice: number; quantity: number; tangible: boolean }> = [
      {
        title: plan.name,
        unitPrice: plan.price,
        quantity: 1,
        tangible: false
      }
    ];

    // Adicionar order bumps selecionadas (nova lgica com mltiplas bumps)
    if (request.orderBumpIds && request.orderBumpIds.length > 0) {
      const selectedBumps = await this.getOrderBumpsByIds(request.orderBumpIds);
      for (const bump of selectedBumps) {
        items.push({
          title: bump.name,
          unitPrice: bump.price,
          quantity: 1,
          tangible: false
        });
      }
    } else if (request.orderBump) {
      // Retrocompatibilidade: order bump nica legada
      items.push({
        title: 'Acesso Antecipado Premium',
        unitPrice: 1990, // R$ 19,90
        quantity: 1,
        tangible: false
      });
    }

    const payload = {
      paymentMethod: 'credit_card',
      amount: request.totalAmount,
      installments: request.installments || 1,
      card: {
        hash: request.cardHash
      },
      customer: {
        name: request.customerName,
        email: request.customerEmail,
        phone: this.sanitizePhone(request.customerPhone),
        document: {
          number: this.sanitizeDocument(request.customerDocument),
          type: 'cpf'
        }
      },
      items,
      // Usar postbackUrl do banco de dados
      postbackUrl: gateway.postbackUrl || undefined
    };

    // 5. Fazer a chamada HTTP para a API JunglePay
    try {
      console.log('[JunglePay] Processando carto de crdito...');
      
      const response = await fetch('https://api.junglepagamentos.com/v1/transactions', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': this.generateAuthHeader(gateway.secretKey)
        },
        body: JSON.stringify(payload)
      });

      if (!response.ok) {
        const errorData = await response.text();
        console.error('[JunglePay] API Error:', response.status, errorData);
        return {
          success: false,
          error: `Erro na API JunglePay: ${response.status} - ${errorData}`,
          code: 'API_ERROR'
        };
      }

      const data: JunglePayTransactionResponse = await response.json();
      console.log('[JunglePay] Resposta:', data.status, data.id);

      // 6. Verificar se foi recusado
      if (data.status === 'refused') {
        const reason = data.refusedReason?.message || 'Carto recusado pela operadora';
        console.error('[JunglePay] Carto recusado:', reason);
        return {
          success: false,
          error: reason,
          code: 'CARD_REFUSED'
        };
      }

      // 7. Criar registro do checkout no banco com o externalId
      const [checkout] = await db.insert(checkouts).values({
        planId: request.planId,
        externalId: String(data.id), // ID da transao na JunglePay
        paymentMethod: 'credit_card',
        orderBump: request.orderBump || (request.orderBumpIds && request.orderBumpIds.length > 0),
        orderBumpIds: request.orderBumpIds || null,
        totalAmount: request.totalAmount,
        customerName: request.customerName,
        customerEmail: request.customerEmail,
        customerDocument: this.sanitizeDocument(request.customerDocument),
        customerPhone: this.sanitizePhone(request.customerPhone),
        status: data.status === 'paid' ? 'paid' : 'pending'
      }).returning();

      // 8. Retornar dados do carto
      return {
        success: true,
        transactionId: data.id,
        checkoutId: checkout.id, // ID do checkout no nosso banco
        status: data.status,
        cardLastDigits: data.card?.lastDigits || '****',
        cardBrand: data.card?.brand || 'unknown',
        installments: data.installments || 1
      };

    } catch (error: any) {
      console.error('[JunglePay] Request Error:', error);
      return {
        success: false,
        error: `Erro ao conectar com JunglePay: ${error.message}`,
        code: 'API_ERROR'
      };
    }
  }
}
