import { db } from '../db';
import { ads, impressions, clicks } from '../db/schema';
import { eq, desc, asc, and, or, sql, gte, lte, isNull } from 'drizzle-orm';

// Types
export type AdType = 'diamond' | 'diamond_block' | 'banner' | 'spot' | 'hero';
export type AdPlacement = 'home_top' | 'home_middle' | 'home_bottom' | 'sidebar' | 'feed_mix' | 'models_grid' | 'model_profile' | 'login' | 'register' | 'feed_model' | 'model_sidebar';
export type AdStatus = 'active' | 'paused' | 'draft';

// Mapeamento de placements vlidos por tipo de anncio
export const VALID_PLACEMENTS_BY_TYPE: Record<AdType, AdPlacement[]> = {
  // Diamond Selection (post style): apenas em feeds de contedo do modelo
  diamond: ['feed_model'],
  
  // Diamond Block (native block): apenas em reas de destaque
  diamond_block: ['home_top', 'home_middle', 'models_grid'],
  
  // Banner: qualquer lugar menos feeds de contedo
  banner: ['home_top', 'home_bottom', 'login', 'register'],
  
  // Ad Spot Small: apenas reas laterais ou secundrias
  spot: ['sidebar', 'model_profile', 'model_sidebar'],
  
  // Hero Carousel: apenas topo da home
  hero: ['home_top']
};

// Funo para obter o placement padro de um tipo
export const getDefaultPlacement = (type: AdType): AdPlacement => {
  return VALID_PLACEMENTS_BY_TYPE[type][0];
};

// Funo para validar se um placement  vlido para um tipo
export const isValidPlacement = (type: AdType, placement: AdPlacement): boolean => {
  return VALID_PLACEMENTS_BY_TYPE[type].includes(placement);
};

export interface CreateAdInput {
  name: string;
  type: AdType;
  placement: AdPlacement;
  status?: AdStatus;
  title: string;
  subtitle?: string;
  ctaText?: string;
  imageUrl?: string;
  link: string;
  category?: string;
  priority?: number;
  startDate?: Date;
  endDate?: Date;
}

export interface UpdateAdInput extends Partial<CreateAdInput> {
  id: number;
}

export interface Ad {
  id: number;
  name: string;
  type: AdType;
  placement: AdPlacement;
  status: AdStatus;
  title: string;
  subtitle: string | null;
  ctaText: string | null;
  imageUrl: string | null;
  link: string;
  category: string | null;
  impressions: number;
  clicks: number;
  priority: number;
  startDate: Date | null;
  endDate: Date | null;
  createdAt: Date | null;
  updatedAt: Date | null;
}

export interface ListAdsResult {
  data: Ad[];
  total: number;
  page: number;
  totalPages: number;
}

export const AdsService = {
  /**
   * Lista todos os anncios com paginao e filtros
   */
  async list(
    page: number = 1, 
    limit: number = 20, 
    filters?: { status?: AdStatus; placement?: AdPlacement; type?: AdType }
  ): Promise<ListAdsResult> {
    const offset = (page - 1) * limit;
    
    // Build conditions
    const conditions = [];
    if (filters?.status) {
      conditions.push(eq(ads.status, filters.status));
    }
    if (filters?.placement) {
      conditions.push(eq(ads.placement, filters.placement));
    }
    if (filters?.type) {
      conditions.push(eq(ads.type, filters.type));
    }

    // Query
    let query = db.select().from(ads).orderBy(desc(ads.createdAt)).limit(limit).offset(offset);
    
    if (conditions.length > 0) {
      query = query.where(and(...conditions)) as any;
    }

    const data = await query;

    // Count total
    let countQuery = db.select({ count: sql<number>`count(*)` }).from(ads);
    if (conditions.length > 0) {
      countQuery = countQuery.where(and(...conditions)) as any;
    }
    const [countResult] = await countQuery;
    const total = Number(countResult?.count || 0);

    return {
      data: data as Ad[],
      total,
      page,
      totalPages: Math.ceil(total / limit)
    };
  },

  /**
   * Busca um anncio por ID
   */
  async getById(id: number): Promise<Ad | null> {
    const [ad] = await db.select().from(ads).where(eq(ads.id, id)).limit(1);
    return (ad as Ad) || null;
  },

  /**
   * Cria um novo anncio
   */
  async create(input: CreateAdInput): Promise<Ad> {
    // Validar e ajustar placement se necessrio
    const type = input.type;
    let placement = input.placement;
    
    if (!isValidPlacement(type, placement)) {
      // Se o placement no  vlido para o tipo, usar o padro
      placement = getDefaultPlacement(type);
      console.warn(`[Ads] Placement "${input.placement}" invlido para tipo "${type}". Usando placement padro: "${placement}"`);
    }
    
    const [ad] = await db.insert(ads).values({
      name: input.name,
      type: input.type,
      placement: placement,
      status: input.status || 'draft',
      title: input.title,
      subtitle: input.subtitle || null,
      ctaText: input.ctaText || null,
      imageUrl: input.imageUrl || null,
      link: input.link,
      category: input.category || null,
      priority: input.priority || 0,
      startDate: input.startDate || null,
      endDate: input.endDate || null,
    }).returning();

    return ad as Ad;
  },

  /**
   * Atualiza um anncio existente
   */
  async update(input: UpdateAdInput): Promise<Ad | null> {
    const { id, ...data } = input;
    
    // Se est alterando o tipo, precisamos validar/ajustar o placement
    if (data.type !== undefined) {
      const currentAd = await this.getById(id);
      if (currentAd) {
        const newType = data.type;
        const currentPlacement = data.placement || currentAd.placement;
        
        if (!isValidPlacement(newType, currentPlacement)) {
          // Ajustar placement para o padro do novo tipo
          data.placement = getDefaultPlacement(newType);
          console.warn(`[Ads] Placement "${currentPlacement}" invlido para tipo "${newType}". Ajustando para: "${data.placement}"`);
        }
      }
    }
    
    // Se est alterando apenas o placement, validar contra o tipo existente
    if (data.placement !== undefined && data.type === undefined) {
      const currentAd = await this.getById(id);
      if (currentAd && !isValidPlacement(currentAd.type, data.placement)) {
        console.warn(`[Ads] Placement "${data.placement}" invlido para tipo "${currentAd.type}". Mantendo placement atual.`);
        delete data.placement;
      }
    }
    
    const updateData: any = {};
    if (data.name !== undefined) updateData.name = data.name;
    if (data.type !== undefined) updateData.type = data.type;
    if (data.placement !== undefined) updateData.placement = data.placement;
    if (data.status !== undefined) updateData.status = data.status;
    if (data.title !== undefined) updateData.title = data.title;
    if (data.subtitle !== undefined) updateData.subtitle = data.subtitle;
    if (data.ctaText !== undefined) updateData.ctaText = data.ctaText;
    if (data.imageUrl !== undefined) updateData.imageUrl = data.imageUrl;
    if (data.link !== undefined) updateData.link = data.link;
    if (data.category !== undefined) updateData.category = data.category;
    if (data.priority !== undefined) updateData.priority = data.priority;
    if (data.startDate !== undefined) updateData.startDate = data.startDate;
    if (data.endDate !== undefined) updateData.endDate = data.endDate;

    const [updated] = await db.update(ads).set(updateData).where(eq(ads.id, id)).returning();
    return (updated as Ad) || null;
  },

  /**
   * Deleta um anncio
   */
  async delete(id: number): Promise<boolean> {
    const [deleted] = await db.delete(ads).where(eq(ads.id, id)).returning();
    return !!deleted;
  },

  /**
   * Alterna o status de um anncio entre active e paused
   */
  async toggleStatus(id: number): Promise<Ad | null> {
    const ad = await this.getById(id);
    if (!ad) return null;

    const newStatus: AdStatus = ad.status === 'active' ? 'paused' : 'active';
    return this.update({ id, status: newStatus });
  },

  /**
   * Incrementa o contador de impresses e registra o evento
   */
  async trackImpression(id: number, metadata?: { placement?: string; userAgent?: string; ip?: string }): Promise<void> {
    try {
      // Usamos uma transao para garantir que ambos os registros sejam feitos
      await db.transaction(async (tx) => {
        // 1. Atualiza o contador denormalizado na tabela de anncios
        await tx.update(ads)
          .set({ impressions: sql`${ads.impressions} + 1` })
          .where(eq(ads.id, id));
        
        // 2. Registra o evento individual na tabela de impresses (agnstico)
        await tx.insert(impressions).values({
          adId: id,
          placement: metadata?.placement || null,
          userAgent: metadata?.userAgent || null,
          ip: metadata?.ip || null,
        });
      });
    } catch (error) {
      console.error(`[AdsService] Erro ao rastrear impresso do anncio ${id}:`, error);
    }
  },

  /**
   * Incrementa o contador de cliques e registra o evento
   */
  async trackClick(id: number, metadata?: { placement?: string; userAgent?: string; ip?: string }): Promise<void> {
    try {
      await db.transaction(async (tx) => {
        // 1. Atualiza o contador denormalizado
        await tx.update(ads)
          .set({ clicks: sql`${ads.clicks} + 1` })
          .where(eq(ads.id, id));
        
        // 2. Registra o evento individual na tabela de cliques
        await tx.insert(clicks).values({
          adId: id,
          placement: metadata?.placement || null,
          userAgent: metadata?.userAgent || null,
          ip: metadata?.ip || null,
        });
      });
    } catch (error) {
      console.error(`[AdsService] Erro ao rastrear clique do anncio ${id}:`, error);
    }
  },

  /**
   * Mtodo genrico para rastreamento de eventos (agnstico)
   */
  async trackEvent(adId: number, type: 'impression' | 'click', metadata?: any): Promise<void> {
    if (type === 'impression') {
      return this.trackImpression(adId, metadata);
    } else if (type === 'click') {
      return this.trackClick(adId, metadata);
    }
  },

  /**
   * Busca anncios ativos para um placement especfico
   * Considera datas de incio e fim se configuradas
   */
  async getActiveByPlacement(placement: AdPlacement, limit: number = 10, track: boolean = false): Promise<Ad[]> {
    const now = new Date();
    
    const result = await db.select().from(ads).where(
      and(
        eq(ads.status, 'active'),
        eq(ads.placement, placement),
        // Start date: null or <= now
        or(isNull(ads.startDate), lte(ads.startDate, now)),
        // End date: null or >= now
        or(isNull(ads.endDate), gte(ads.endDate, now))
      )
    ).orderBy(desc(ads.priority), desc(ads.createdAt)).limit(limit);

    const formattedResult = result as Ad[];

    // Rastreamento automtico se solicitado
    if (track) {
      for (const ad of formattedResult) {
        this.trackImpression(ad.id, { placement }).catch(() => {});
      }
    }

    return formattedResult;
  },

  /**
   * Busca anncios ativos para mltiplos placements
   */
  async getActiveByPlacements(placements: AdPlacement[], track: boolean = false): Promise<Record<AdPlacement, Ad[]>> {
    const result: Record<string, Ad[]> = {};
    
    for (const placement of placements) {
      result[placement] = await this.getActiveByPlacement(placement, 10, track);
    }
    
    return result as Record<AdPlacement, Ad[]>;
  },

  /**
   * Formata label do placement para exibio
   */
  formatPlacementLabel(placement: AdPlacement): string {
    const labels: Record<AdPlacement, string> = {
      'home_top': 'Home (Topo)',
      'home_middle': 'Home (Meio)',
      'home_bottom': 'Home (Rodap)',
      'sidebar': 'Sidebar',
      'feed_mix': 'Feed Mix',
      'models_grid': 'Grid de Modelos',
      'model_profile': 'Perfil de Modelo',
      'login': 'Pgina de Login',
      'register': 'Pgina de Registro',
      'feed_model': 'Feed de Modelo',
      'model_sidebar': 'Sidebar de Modelo'
    };
    return labels[placement] || placement;
  },

  /**
   * Formata label do tipo para exibio
   */
  formatTypeLabel(type: AdType): string {
    const labels: Record<AdType, string> = {
      'diamond': 'Diamond (Post)',
      'diamond_block': 'Diamond (Block)',
      'banner': 'Banner',
      'spot': 'Ad Spot',
      'hero': 'Hero Carousel'
    };
    return labels[type] || type;
  }
};
